// Default headers
function defaultHeaders(headerFields) {
    var headers = new Headers()
    headers.append("accept", "application/vnd.github.v3+json")
    headers.append("authorization", "token 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")

    if (headerFields != null) {
        Object.keys(headerFields).forEach( name =>
            headers.append(name, headerFields[name])
        )
    }

    return headers
}


// GET

export async function get(url, headerFields) {
    const requestOptions = {
        method: 'GET',
        headers: defaultHeaders(headerFields)
    }

    return await fetch(url, requestOptions)
}

export async function getJson(url, headerFields) {
    const response = await get(url, headerFields)
    return await response.json()
}


// PATCH

export async function patch(url, headerFields, parameters) {
    const requestOptions = {
        method: 'PATCH',
        headers: defaultHeaders(headerFields),
        body: JSON.stringify(parameters)
    }

    return await fetch(url, requestOptions)
}

export async function patchJson(url, headerFields, parameters) {
    const response = await patch(url, headerFields, parameters)
    return await response.json()
}
