import { Component } from 'react'
import { getJson, patchJson } from '../common/utils/services.js'
import React from 'react';

export default function Settings() {
    var index = 1
    const dom = Object.keys(repos).map(name =>
        <RepoItem name={name} gitPath={repos[name]} index={index++} />
    )

    return (
        <>
            <h1>Settings</h1>
            <h2>Update default branch</h2>
            {dom}
        </>
    )
}

class RepoItem extends Component {
    constructor(props) {
        super(props)

        this.state = {
            name: props.name,
            gitPath: props.gitPath,
            index: props.index,
            branchName: ''
        }

        this.updateDefaultBranch = this.updateDefaultBranch.bind(this)
        this.getRepoInfo = this.getRepoInfo.bind(this)
    }

    componentDidMount() {
        this.getRepoInfo()
    }

    render() {
        const inputId = `input.${this.state.name}`
        const content = `#${this.state.index}. ${this.state.name} - ${this.state.branchName}`
        return <div>{content} - <input id={inputId} /> - <button onClick={this.updateDefaultBranch}>update</button></div>
    }

    updateDefaultBranch() {
        const inputId = `input.${this.state.name}`
        const branchName = document.getElementById(inputId).value

        const url = `https://api.github.com/repos/${this.state.gitPath}`
        patchJson(url, null, { 'default_branch': branchName }).then(json => {
            this.setState({
                branchName: branchName
            })
            document.getElementById(inputId).value = ''
            // alert(JSON.stringify(json))
        }).catch(error => alert(error))
    }

    getRepoInfo() {
        // https://docs.github.com/en/rest/reference/repos#get-a-repository
        const url = `https://api.github.com/repos/${this.state.gitPath}`
        getJson(url).then(json => {
            this.setState({
                branchName: json['default_branch']
            })
        }).catch(error => alert(error))
    }
}

const repos = {
    'TKRemoteConfig': 'tikivn/TKRemoteConfig',
    'TKNavigator': 'tikivn/TKNavigator',
    'TKAddress': 'tikivn/TKAddress',
    'TKProductDetails': 'tikivn/TKProductDetails',
    'TKCommonAppKit': 'tikivn/TKCommonAppKit',
    'TKCommunity': 'tikivn/TKCommunity',
    'TKAppClient': 'tikivn/TKAppClient',
    'TKHome': 'tikivn/TKHome',
    'TKAppNotification': 'tikivn/TKAppNotification',
    'TKListing': 'tikivn/TKListing',
    'TKCouponService': 'tikivn/TKCouponService',
    'TKNetworkManager': 'tikivn/TKiOSNetworkManager',
    'TKCheckout': 'tikivn/TKCheckout',
    'TKUIKit': 'tikivn/TKUIKit',
    'TKSocketManager': 'tikivn/TKiOSSocket',
    'TKLive': 'tikivn/TKLive-iOS',
    'TKAppTracking': 'tikivn/TKAppTracking',
    'TKTrackingSDK': 'tikivn/TikiTrackingSDK',
    'DLSKit': 'tikivn/TKiOSDLSKit',
    'TKDebugMenuKit': 'tikivn/TKDebugMenu'
}
